import { createStore } from 'vuex'

export default createStore({
  state: {
    //baseUrl:'http://localhost:3000/',
    //baseUrl:'http://192.168.43.251:3000/',
    baseUrl:'https://cupcake.sassayer.com/',
    products:{
      created:false,
      elt:[]
    },
    currProduct: null
  },
  mutations: {
    setProducts (state,playload) {
      state.products.elt = playload
      state.products.created = true
    },
    setCurrProduct (state,playload) {
      state.currProduct = playload
    }
  },
  actions: {
  },
  modules: {
  }
})
