import LNavBar from '@/components/lNavBar/LNavBar'
import LFooter from '@/components/lFooter/LFooter'
import AlertG from '@/components/alertG/AlertG'
import VisualizerA from './VisualizerA'
import LLoading1 from '@/components/lLoading1/LLoading1'
import Buy from './Buy'
import Whatsapp from '@/components/whatsapp/Whatsapp'

export default {
  name: 'landing',
  components: {
    LNavBar,
    LFooter,
    AlertG,
    VisualizerA,
    LLoading1,
    Buy,
    Whatsapp
  },
  data () {
    return {
      loading1:false,
      infos: {},
      boxes:[],
      persos:[],
      deliveries:['Livraison normale (1000F)', 'Livraison express (1500F)'],
      box:'',
      perso:'',
      delivery:'Livraison normale (1000F)',
      images:[],
      //------- to send variable --------
      amount:1
    }
  },
  methods: {
    ordering () {
      let tr = this.delivery == this.deliveries[0] ? 1000 : 1500
      this.$refs.buy.show({name:this.infos.name,amount:this.amount,
        price:this.infos.price, transport:tr, box:this.box,perso:this.perso, productId:this.infos.id })
    },
    add (s) {
      if(s>0) this.amount ++
      else {
        if(this.amount != 1) this.amount --
      }
    },
    getInfos () {
      this.loading1 = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'client.php', {
        aim:'getOne',
        id:this.$route.params.id
      })
      .then(({data}) => {
        this.loading1 = false
        this.infos = data
        this.setImages()
      })
      .catch((error) => {
        console.log(error)
        this.loading1 = false
      })
    },
    setImages () {
      let bUrl = this.$store.state.baseUrl + 'images/'
      if(this.infos.img0 != '' ) this.images.push(bUrl+this.infos.img0)
      if(this.infos.img1 != '' ) this.images.push(bUrl+this.infos.img1)
      if(this.infos.img2 != '' ) this.images.push(bUrl+this.infos.img2)
      if(this.infos.img3 != '' ) this.images.push(bUrl+this.infos.img3)
      if(this.infos.img4 != '' ) this.images.push(bUrl+this.infos.img4)
      if(this.infos.img5 != '' ) this.images.push(bUrl+this.infos.img5)
      this.$refs.visualizer.init()
      //--- set boxes ---------------------------
      if(this.infos.box1 != '') this.boxes.push(this.infos.box1)
      if(this.infos.box2 != '') this.boxes.push(this.infos.box2)
      if(this.infos.box3 != '') this.boxes.push(this.infos.box3)
      if(this.boxes.length == 0) this.boxes.push('Boite classique')
      this.box = this.boxes[0]
      //--- set persos ------------------------------------------
      if(this.infos.perso1 != '') this.persos.push(this.infos.perso1)
      if(this.infos.perso2 != '') this.persos.push(this.infos.perso2)
      if(this.infos.perso3 != '') this.persos.push(this.infos.perso3)
      if(this.persos.length == 0) this.persos.push('Aucune perso dispo')
      this.perso = this.persos[0]
    }
  },
  mounted () {
    if(this.$store.state.currProduct == null) {
      this.getInfos()
    }
    else {
      this.infos = this.$store.state.currProduct
      this.setImages()
    }
  }
}
