import LNavBar from '@/components/lNavBar/LNavBar'
import LFooter from '@/components/lFooter/LFooter'
import AlertG from '@/components/alertG/AlertG'
import RSlider from '@/components/rSlider/RSlider'
import LPublication from '@/components/lPublication/LPublication'
import LLoading1 from '@/components/lLoading1/LLoading1'
import Whatsapp from '@/components/whatsapp/Whatsapp'

export default {
  name: 'landing',
  components: {
    LNavBar,
    LFooter,
    AlertG,
    RSlider,
    LPublication,
    LLoading1,
    Whatsapp
  },
  data () {
    return {
      loading1:false,
      DispProduct:false,
      elements:[
    ]
    }
  },
  methods: {
    toProduct () {
      this.$store.commit('setCurrProduct',this.elements[0])
      this.$router.push({name:'cake', params: {id: this.elements[0].id }})
    },
    scrollMeTo(place) {
      let top = 0
      top = this.$refs[place].offsetTop
      //window.scrollTo(0, top-90)
      window.scrollTo({top:top-90,behavior:'smooth'})
    },
    getObjects () {
      this.loading1 = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'client.php', {
        aim:'get'
      })
      .then(({data}) => {
        this.loading1 = false
        this.$store.commit('setProducts',data.elts)
        this.elements = data.elts
      })
      .catch((error) => {
        console.log(error)
        this.loading1 = false
      })
    }
  },
  mounted () {
    if(!this.$store.state.products.created) {
      this.getObjects()
    }
    else this.elements = this.$store.state.products.elt
  }
}
