export default {
    name: 'login',
    data () {
        return {
            erreur: false,
            login: '',
            password: '',
            Display: true
        }
    },
    methods: {
        validate () {
            //alert(this.$store.state.baseUrl)
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type: 'login',
                login: this.login,
                password: this.password
            })
            .then(({data}) => {
                if(data == 'exist') {
                    this.Display = false
                    this.$emit('closed')
                }
                else this.erreur = true
            })
            .catch((error) => {
                console.log(error)
            })
          }
    }
}