<template>
    <div v-show="Display" @click="closing" ref='main' class="Buy">
        <div class="BuyChild">
              <h3> {{ pl.amount }}x {{ pl.name }}</h3>
              <div>prix: {{ pl.amount*pl.price }} FCFA + {{ pl.transport }} FCFA (transport)</div>
              <input v-model="ordererName" type="text" placeholder="Entrez votre nom"/>
              <input v-model="ordererTel" type="number" placeholder="Numéro téléphone"/>
              <input v-model="ordererQuater" type="text" placeholder="Quartier"/>
              <div @click="order" class="BuyButton">COMMANDEZ</div>
        </div>
        <AlertG ref='alertG'/>
        <AlertG ref='alertGA' @okay='Display = false'/>
        <LLoading :visible='loading'/>
    </div>
</template>
<style>

.Buy {
  position: fixed;
  z-index: 3;
  left:0px;
  top:0px;
  width: 100%;
  height: 100%;
  display: flex;justify-content: center;align-items: center;
  background-color:rgb(0,0,0,0.5);
}
.BuyChild {
  width: 290px;
  max-width: 90%;
  background-color:white;
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.BuyChild > h3 + div {
  text-align: left;
  margin-bottom: 10px;
}
.BuyChild > input {
  width:100%;
  margin-bottom: 10px;
  border: solid 1px silver;
  height: 35px;
  padding-left: 15px;
  font-family: KingstownNF;
}
.BuyButton {
  margin-top: 10px;
  height:  45px;
  display: flex;justify-content: center;align-items: center;
  color:white;
  background-color:#3A0A07;
  cursor: pointer;
}
</style>
<script>
import AlertG from '@/components/alertG/AlertG'
import LLoading from '@/components/lLoading/LLoading'

export default {
  name: 'Buy',
  components: {
    AlertG,
    LLoading
  },
  data () {
    return {
      Display:false,
      loading:false,
      pl:{name:'Name od cake',amount:3,price:1000,transport:1000},
      ordererName:'',
      ordererTel:'',
      ordererQuater:''
    }
  },
  methods: {
    closing (event) {
      if( event.target == this.$refs.main ) this.Display = false
    },
    show (pl) {
      this.pl = pl
      this.Display = true
    },
    order () {
      if(this.ordererName == '' || this.ordererTel == '' || this.ordererQuater == '') {
        this.$refs.alertG.displaying('Veuillez remplir correctement')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'client.php', {
        ...this.pl,
        aim:'order',
        ordererName:this.ordererName,
        ordererTel: this.ordererTel,
        ordererQuater: this.ordererQuater
      })
      .then(({data}) => {
        if(data=='tooMore') alert('Vous avez dépassé la limite de commande Journalière, Essayé de nous contacté via notre numéro de téléphone')
        else {
          this.$refs.alertGA.displaying('Votre commande a été éffectuée avec <a>SUCCESS</a>, nous vous contacterons ultérieurement.')
        }
        this.loading = false
      })
      .catch((error) => {
        console.log(error)
        this.loading = false
      })
    },
  }
}

</script>
