<template>
    <div class="admin">
        <Login @closed='getObjects'/>
        <div class="adminTitle">Admin</div>
        <div class="adminMain">
            <div class="landingToForm">
                <div class="landingTFLeft">
                    <div class="landingTFElt">
                        <div>
                            <span>Publier un produit</span>
                            Ajouter un nouveau produit sur le site 
                        </div>
                        <span><span @click="$refs.stepsFormLost.Display=true">Publier</span></span>
                    </div>
                </div>
            </div>

            <div class="adminProduct" v-show="eltsList.length > 0">
                <span>Mes objects</span>
                <div v-for="(elt,index) in eltsList" :key="index" class="landingMRElt">
                    <div class="landingMREltInfos">
                        <div :style="{backgroundImage:'url('+setImage(elt)+')'}" class="landingMREltImg">

                        </div>
                        <div>
                            <span class="landingMRCity">{{ elt.name }}</span>
                            <span>{{ elt.price }} FCFA</span>
                            <span>{{ elt.type }}</span>
                        </div>
                    </div>
                    <div class="landingMREltMenu">
                        <div class="landingMRElTFermer" @click="edit(elt)">Editer</div>
                        <div @click="deleting(index)">Suppr</div>
                    </div>
                </div>
            </div>
            <div class="adminProduct" v-show="eltsList.length > 0">
                <span>Mes comandes</span>
                <div v-for="(elt,index) in orders" :key="index" class="landingMRElt">
                    <div class="landingMREltInfos">
                        <div>
                            <span class="adminOName">{{ elt.productName }}</span>
                            <span>{{ elt.price }} FCFA</span>
                            <span>{{ elt.ordererName.slice(0,25) }}</span>
                        </div>
                    </div>
                    <div class="landingMREltMenu">
                        <div class="adminOVoir" >
                            <div>{{ setDate(elt.date) }}</div><span @click="seeDetails(elt)">Voir</span>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <div v-show="false" style="padding:20px" class="adminElt">
            <div class="adminBTitle">Change password and login </div>
            <div class="adminLogin">
                <input v-model="login" type="text" placeholder="Login"/>  
                <input v-model="password" type="password" placeholder="Password"/>
                <input v-model="newLogin" type="text" placeholder="New login"/> 
                <input v-model="newPassword" type="password" placeholder="New password"/>
                <div @click="changeLogin">Change</div>
            </div>
        </div>
        <div v-show="DisplayDetails" @click="detailsClick" ref="details" class="adminPDetails">
            <div>
                <div>Nom<span>{{ current.ordererName }}</span></div>
                <div>Tel<span>{{ current.ordererTel }}</span></div>
                <div>Quater<span>{{ current.ordererQuater }}</span></div>
                <div style="margin-bottom:20px">Commandé le<span>{{ setDateA(current.date) }}</span></div>

                <div>Nom produit<span>{{ current.productName }}</span></div>
                <div>Type de boite<span>{{ current.box }}</span></div>
                <div>Personaisation<span>{{ current.perso }}</span></div>
                <div>Livraison<span>{{ current.delivery }} FCFA</span></div>
                <div>Quantité<span>{{ current.amount }}</span></div>
                <div>Prix<span>{{ current.price }} FCFA</span></div>
                <div>Total<span>{{ current.price*current.amount }} FCFA</span></div>
            </div>    
        </div>
        <StepsForm ref='stepsFormLost' type="lost" @newElt='getObjects'/>
        <StepsForm ref='stepsFormEditL' type="lost" edit="yes"/>
        <Confirm @clicked="(verdict)=>deleteMe(toDeleteIndex,verdict)" ref="confirm"/>
        <LLoading :visible='loading' />
    </div>
</template>
<script src='./admin.js'>
</script>
<style src='./admin.css'>
</style>
