export default {
  name: 'lPublication',
  props: {
    datas: {
      type: Object,
      default: {name:'operum ipsum apsomun',price:12000},
    }
  },
  data () {
    return {
      img:''
    }
  },
  methods: {
    toProduct () {
      this.$store.commit('setCurrProduct',this.datas)
      this.$router.push({name:'cake', params: {id: this.datas.id }})
    }
  },
  mounted () {
    let bUrl = this.$store.state.baseUrl + 'images/'
    if(this.datas.img0 != '' ) this.img = bUrl+this.datas.img0
    else if(this.datas.img1 != '' ) this.img = bUrl+this.datas.img1
    else if(this.datas.img2 != '' ) this.img = bUrl+this.datas.img2
    else if(this.datas.img3 != '' ) this.img = bUrl+this.datas.img3
    else if(this.datas.img4 != '' ) this.img = bUrl+this.datas.img4
    else if(this.datas.img5 != '' ) this.img = bUrl+this.datas.img5
  }
}
