import LLoading from '@/components/lLoading/LLoading'
import Login from './login/Login'
import StepsForm  from '@/components/stepsForm/StepsForm'
import Confirm from '@/components/confirm/Confirm'

export default {
  name: 'admin',
  components: {
    LLoading,
    Login,
    StepsForm,
    Confirm
  },
  data () {
    return {
      DisplayDetails: false,
      loading: false,
      login:'',
      password:'',
      newLogin:'',
      newPassword:'',

      mois: ['Janvier','Février','Mars','Avril','Mai','Juin','Juillet',
      'Aout','Septembre','Octobre','Novembre','Décembre'],
      /*------ for product-----*/
      eltsList:[],
      orders:[],
      current:{},
      toDeleteIndex:0,
      bUrl:this.$store.state.baseUrl + 'images/',
    }
  },
    methods: {
      seeDetails (elt) {
        this.current = elt
        this.DisplayDetails = true
      },
      detailsClick (event) {
        if(event.target == this.$refs.details) this.DisplayDetails = false
      },
      setDate (dat) {
        let d = new Date(dat)
        return d.getDate()+' '+this.mois[d.getMonth()] +', '+ d.getHours() + 'H:'
        + (d.getMinutes() < 10 ? '0'+d.getMinutes() : d.getMinutes())
      },
      setDateA (dat) {
        let d = new Date(dat)
        return d.getDate()+' '+this.mois[d.getMonth()] +' '+ d.getFullYear()+' à '+ + d.getHours() + 'H:'
        + (d.getMinutes() < 10 ? '0'+d.getMinutes() : d.getMinutes())
      },
      setImage (elt) {
        if(elt.img0 !='') return this.bUrl + elt.img0
        else if(elt.img1 !='') return this.bUrl + elt.img1
        else if(elt.img2 !='') return this.bUrl + elt.img2
        else return this.bUrl + elt.img3
        //(elt.img0 !='' ? bUrl + elt.img0 : ( elt.img1 !='' ? bUrl + elt.img1 : (elt.img2 !='' ? bUrl + elt.img2 : bUrl+elt.img3 )))
      },
      edit (elt) {
        this.$refs.stepsFormEditL.loadObject(elt)
      },
      deleting (index) {
        this.toDeleteIndex = index
        this.$refs.confirm.show('Etes vous sure de vouloir supprimer ?')
      },
      deleteMe (index,verdict) {
        if(verdict == 'no') return
        this.loading = true
        const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'publication.php', {
          aim:'delete',
          id:this.eltsList[index].id
        })
          .then(() => {
            this.eltsList.splice(index,1)
            this.loading = false
          })
          .catch((error) => {
            console.log(error)
            this.loading = false
          })
      },


      changeLogin () {
          this.loading = true
          const axios = require('axios')
          axios.post(this.$store.state.baseUrl + 'inscription.php', {
              type:'change',
              login: this.login,
              password: this.password,
              newPassword: this.newPassword,
              newLogin: this.newLogin
          })
          .then(({data}) => {
              console.log(data)
              this.loading = false
              if(data == 'done') {
                  alert('done')
              }
              else {
                alert(data)
              }
          })
          .catch((error) => {
              console.log(error)
              this.loading = false
          })
      },

      getObjects () {
        this.loading = true
        const axios = require('axios')
        axios.post(this.$store.state.baseUrl + 'publication.php', {
          aim:'get'
        })
          .then(({data}) => {
            this.loading = false
            this.eltsList = data.elts
            this.orders = data.orders
          })
          .catch((error) => {
            console.log(error)
            this.loading = false
          })
      }
    }
}