import ImgUpload from '@/components/imgUpload/ImgUpload'
import AlertG from '@/components/alertG/AlertG'
import LLoading1 from '@/components/lLoading1/LLoading1'

export default {
  name: 'lLoading',
  props: {
    type: {
      default:'lost',
      type:String
    },
    edit: {
      default:'not',
      type:String
    }
  },
  emits:["connexion"],
  components: {
    ImgUpload,
    AlertG,
    LLoading1
  },
  computed: {
    Position () {
      return this.position + '%'
    }
  },
  data () {
    return {
      loading:false,
      Display:false,
      position:0,
      transition:'left 0.7s ease-out',
      infos:{id:-1},
      toSend0:{},
      toSend1:{},
      toSend2:{},
      slides: [
        {
          title:"Quel est cet produit ?",
          subtitle:"Veillez remplir les informations sur le produit.",
          selects:[
            {name:"type", value:"Cupcake",options:['Cupcake','Gateau']}
          ],
          inputs: [
            {name:"name",type:"text", value:"",placeholder:"Nom"}
          ],
          textareas: [
            {name:"description", value:"",placeholder:"Description"}
          ],
          images:[]
        },
        {
          title:"Plus d'information",
          subtitle:"Veillez remplir les informations",
          selects:[
          ],
          inputs: [
            {name:"price",type:"number", value:"",placeholder:"Prix"},

            {name:"box1",type:"text", value:"",placeholder:"Type de boite 1"},
            {name:"box2",type:"text", value:"",placeholder:"Type de boite 2"},
            {name:"box3",type:"text", value:"",placeholder:"Type de boite 3"},

            {name:"perso1",type:"text", value:"",placeholder:"Personalisation 1"},
            {name:"perso2",type:"text", value:"",placeholder:"Personalisation 2"},
            {name:"perso3",type:"text", value:"",placeholder:"Personalisation 3"}
          ],
          textareas: [
            {name:"recipe", value:"",placeholder:"recette de cuisine"}
          ],
          images:[]
        },
        {
          title:"Quelques images du produit",
          subtitle:"Ajouter des images du produit (obligatioire)",
          selects:[
          ],
          inputs: [
          ],
          textareas: [
          ],
          images:[{name:'lost'},{name:'lost'},{name:'lost'},{name:'lost'},{name:'lost'},{name:'lost'}]
        }
      ]
    }
  },
  methods: {
    touching (event) {
      let elt = document.getElementById('IstepsFormP'+this.type+this.edit)
      //alert(typeof elt.contains(event.target))
      this.$refs["stepsFormI"][0].dontClear = false
      if(!elt.contains(event.target)) {
        //alert('ff')
        this.$refs["stepsFormI"][0].blurred()
      }
    },
    move (i) {
      if ((this.position >= -200 && i === -1) || (this.position < 0 && i === 1)) {
        this.position += i * 100
      }
    },
    loadObject (elt) {
      this.infos = elt
      let slides = this.slides
      slides[0].selects[0].value = this.infos.type
      slides[0].inputs[0].value = this.infos.name
      slides[0].textareas[0].value = this.infos.description
      slides[1].inputs[0].value = this.infos.price
      
      slides[1].inputs[1].value = this.infos.box1
      slides[1].inputs[2].value = this.infos.box2
      slides[1].inputs[3].value = this.infos.box3
      
      slides[1].inputs[4].value = this.infos.perso1
      slides[1].inputs[5].value = this.infos.perso2
      slides[1].inputs[6].value = this.infos.perso3


      slides[1].textareas[0].value = this.infos.recipe

      let bsrl = this.$store.state.baseUrl + 'images/'
      this.$refs.images[0].url = this.infos.img0 == '' ? '' : (this.infos.img0.includes('://') ?'':bsrl) + this.infos.img0
      this.$refs.images[1].url = this.infos.img1 == '' ? '' : (this.infos.img1.includes('://') ?'':bsrl) + this.infos.img1
      this.$refs.images[2].url = this.infos.img2 == '' ? '' : (this.infos.img2.includes('://') ?'':bsrl) + this.infos.img2
      this.$refs.images[3].url = this.infos.img3 == '' ? '' : (this.infos.img3.includes('://') ?'':bsrl) + this.infos.img3
      this.$refs.images[4].url = this.infos.img4 == '' ? '' : (this.infos.img3.includes('://') ?'':bsrl) + this.infos.img4
      this.$refs.images[5].url = this.infos.img5 == '' ? '' : (this.infos.img3.includes('://') ?'':bsrl) + this.infos.img5
      this.Display = true
    },
    updateObject () {
      let slides = this.slides
      this.infos.type = slides[0].selects[0].value
      this.infos.name = slides[0].inputs[0].value
      this.infos.description = slides[0].textareas[0].value
      this.infos.price = slides[1].inputs[0].value

      this.infos.box1 = slides[1].inputs[1].value
      this.infos.box2 = slides[1].inputs[2].value
      this.infos.box3 = slides[1].inputs[3].value
      
      this.infos.perso1 = slides[1].inputs[4].value
      this.infos.perso2 = slides[1].inputs[5].value
      this.infos.perso3 = slides[1].inputs[6].value

      this.infos.recipe = slides[1].textareas[0].value
      
      let bsrl = this.$store.state.baseUrl + 'images/'
      this.infos.img0 = this.$refs.images[0].url.replace(bsrl,'' )
      this.infos.img1 = this.$refs.images[1].url.replace(bsrl,'' )
      this.infos.img2 = this.$refs.images[2].url.replace(bsrl,'' )
      this.infos.img3 = this.$refs.images[3].url.replace(bsrl,'' )
      this.infos.img4 = this.$refs.images[4].url.replace(bsrl,'' )
      this.infos.img5 = this.$refs.images[5].url.replace(bsrl,'' )
    },
    close () {
      this.Display = false
      this.transition = 'none'
      this.position = 0
      this.transition = 'left 0.7s ease-out'
    },
    //------------ validation --------------------------------------
    validation () {
      let step = Math.abs(this.position/100)
      let slide = this.slides[step]

      if(step === 0) {
        if (slide.inputs[0].value.length < 3) {
          this.$refs.alertG.displaying('Entrez un nom valide')
          return false
        }
        if (slide.textareas[0].value.length < 4) {
          this.$refs.alertG.displaying('Description trop courte (4 charactères minimun).')
          return false
        }
        else if (slide.textareas[0].value.length >= 350) { 
          this.$refs.alertG.displaying('Description trop longue (350 charactère maximun).')
          return false
        }
        this.toSend0 = {type:slide.selects[0].value,name: slide.inputs[0].value,description:slide.textareas[0].value}
        this.move(-1)
        return true
      }
      else if(step === 1) {
        let regex = /^[0-9]{0,11}$/
        if (!regex.test(slide.inputs[0].value) || slide.inputs[0].value === '') {
          this.$refs.alertG.displaying(slide.inputs[0].placeholder+' incorrect')
          return false
        }
        this.toSend1 = {price:slide.inputs[0].value,
          box1: slide.inputs[1].value,box2: slide.inputs[2].value,box3: slide.inputs[3].value,
          perso1: slide.inputs[4].value,perso2: slide.inputs[5].value,perso3: slide.inputs[6].value,
          recipe:slide.textareas[0].value
        }
        this.move(-1)
        return true
      }
      else if(step === 2) {
        //---- if not connected ----------
        /*if(!this.$store.state.user.connected) {
          this.$emit('connexion')
          return
        }*/
        
        const axios = require('axios')
        let i = 0
        let formData = new FormData()
        formData.append('aim', 'newObject')
        formData.append('id', this.infos.id)
        if(this.infos.id != -1) {
          formData.append('imgName', this.infos.imgName)
          formData.append('img0', this.infos.img0)
          formData.append('img1', this.infos.img1)
          formData.append('img2', this.infos.img2)
          formData.append('img3', this.infos.img3)
          formData.append('img4', this.infos.img4)
          formData.append('img5', this.infos.img5)
        }
        //----- let's append objects properties -----------
        let toSend ={...this.toSend0,...this.toSend1}
        for(const index in toSend) {
          formData.append(index,toSend[index])
        }

        let k=0,images = this.$refs.images
        
        //---- return if no image ---------------------
        for(i=0;i<images.length;i++) {
          if(images[i].url =='') k++
        }
        if(k==6) {
          this.$refs.alertG.displaying('Choisir au moins une image')
          return
        }

        //---- let's append images -----------
        for(i=0;i<images.length;i++) {
          if(images[i].modified && images[i].url !='') {
            formData.append('image'+i, images[i].file)
          }
          // image to be deleted -------
          else if(images[i].modified && images[i].url == '') {
            formData.append('image'+i, 'toDelete')
          }
        }
        //--------- now sending the request ----------------------
        this.loading = true
        axios.post(this.$store.state.baseUrl + 'publication.php', formData, {
            headers: {
            'Content-Type': 'multipart/form-data'
            }
        })
        .then(({data}) => {
          console.log(data)
          this.loading = false
          if(this.infos.id != -1) this.updateObject()
          else {
            this.infos = {id:-1}
            this.reseting()
            this.$emit('newElt')
          }
          this.$refs.alertGC.displaying('Succes!')
        })
        .catch((error) => {
          console.log(error)
          this.loading = false  
        })
      }
    },
    reseting () {
    let slides = this.slides
    slides[0].selects[0].value = 'Cupcake'
    slides[0].inputs[0].value = ''
    slides[0].textareas[0].value = ''
    slides[1].inputs[0].value = ''
    
    slides[1].inputs[1].value = ''
    slides[1].inputs[2].value = ''
    slides[1].inputs[3].value = ''
    
    slides[1].inputs[4].value = ''
    slides[1].inputs[5].value = ''
    slides[1].inputs[6].value = ''


    slides[1].textareas[0].value = ''

    this.$refs.images[0].url = ''
    this.$refs.images[1].url = ''
    this.$refs.images[2].url = ''
    this.$refs.images[3].url = ''
    this.$refs.images[4].url = ''
    this.$refs.images[5].url = ''

    }
  }
}
